"use client";

import { useState, useEffect } from "react";
import Chat from "../ui/chat";
import MobileNav from "../ui/dashboard/mobile-nav";
import SideNav from "../ui/dashboard/sidenav";

export default function Layout({ children }) {
    const [activeMenu, setActiveMenu] = useState("Q&A");
    const [classType, setClassType] = useState(0);
    const [isMobileNavVisible, setIsMobileNavVisible] = useState(false);
    const [isMobile, setIsMobile] = useState(false);

    // ÌôîÎ©¥ ÌÅ¨Í∏∞ Î≥ÄÍ≤Ω Í∞êÏßÄ
    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth >= 1024) {
                setIsMobile(false); // Îç∞Ïä§ÌÅ¨ÌÜ± ÏÉÅÌÉú
                setIsMobileNavVisible(false); // Î™®Î∞îÏùº Î©îÎâ¥ Îã´Í∏∞
            } else {
                setIsMobile(true); // Î™®Î∞îÏùº ÏÉÅÌÉú
            }
        };

        handleResize(); // Ï≤´ Î°úÎìú Ïãú ÌôîÎ©¥ ÌÅ¨Í∏∞ Î∞òÏòÅ
        window.addEventListener("resize", handleResize);

        return () => {
            window.removeEventListener("resize", handleResize); // Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Î¶¨Ïä§ÎÑà Ï†úÍ±∞
        };
    }, []);

    // ÌñÑÎ≤ÑÍ±∞ Î©îÎâ¥ ÌÅ¥Î¶≠ Ïãú ÌëúÏãú ÌÜ†Í∏Ä
    const toggleMobileNav = () => {
        setIsMobileNavVisible(!isMobileNavVisible);
    };

    // Î©îÎâ¥ ÌÅ¥Î¶≠ Ïãú ÌéòÏù¥ÏßÄ Ïù¥Îèô + Î©îÎâ¥ Îã´Í∏∞
    const handleMenuClick = (menu) => {
        setActiveMenu(menu); // ÌòÑÏû¨ ÌôúÏÑ±ÌôîÎêú Î©îÎâ¥ ÏóÖÎç∞Ïù¥Ìä∏
        setIsMobileNavVisible(false); // Î™®Î∞îÏùº ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Îã´Í∏∞
    };

    return (
        <div className="flex h-screen">
            {/* ÏÇ¨Ïù¥ÎìúÎ∞î (lg Ïù¥ÏÉÅÏóêÏÑúÎßå Î≥¥ÏûÑ) */}
            <div className="hidden lg:block lg:w-[25%] bg-[#F0F0F0]">
                <SideNav
                    setClassType={setClassType}
                    activeMenu={activeMenu}
                    setActiveMenu={setActiveMenu}
                />
            </div>

            {/* Î©îÏù∏ ÏΩòÌÖêÏ∏† ÏòÅÏó≠ */}
            <div className="flex-grow flex flex-col lg:w-[75%]">
                {/* ÏÉÅÎã®Î∞î (Î™®Î∞îÏùº Ï†ÑÏö©) */}
                {isMobile && (
                    <div className="flex items-center justify-between p-4 border-b bg-white shadow-sm">
                        <button
                            onClick={toggleMobileNav}
                            className="p-2 bg-gray-200 rounded-md"
                        >
                            ‚ò∞
                        </button>
                        {/* ÌòÑÏû¨ ÌôúÏÑ±ÌôîÎêú Î©îÎâ¥ ÌëúÏãú */}
                        <div className="text-lg font-semibold">
                            {activeMenu}
                        </div>
                        {/* Ïò§Î•∏Ï™Ω Ïó¨Î∞±ÏùÑ ÎßûÏ∂îÍ∏∞ ÏúÑÌïú Îπà ÏöîÏÜå */}
                        <div className="w-8"></div>
                    </div>
                )}

                {/* Î™®Î∞îÏùº ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò (ÌñÑÎ≤ÑÍ±∞ Î©îÎâ¥ ÏÑ†ÌÉù Ïãú ÌëúÏãú) */}
                {isMobileNavVisible && isMobile && (
                    <div className="bg-white p-4 border-b shadow-sm">
                        <MobileNav
                            activeMenu={activeMenu}
                            setActiveMenu={(menu) =>
                                handleMenuClick(menu, `/${menu.toLowerCase()}`)
                            }
                        />
                    </div>
                )}

                {/* Î©îÏù∏ ÏΩòÌÖêÏ∏† */}
                <div className="flex-grow overflow-y-auto p-6 md:p-12">
                    {children}
                </div>

                {/* ÌïòÎã® Ï±ÑÌåÖ ÏòÅÏó≠ */}
                <div className="p-6 border-t bg-white">
                    <Chat
                        activeMenu={activeMenu}
                        setActiveMenu={setActiveMenu}
                    />
                </div>
            </div>
        </div>
    );
}
